<template>
	<view>
		<DetailFooter :art_id="art_id" :artitleData="artitleData"></DetailFooter>
		<DetailContent :artitleData="artitleData"></DetailContent>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				art_id:null,
				artitleData:null
			}
		},
		onLoad(e) {
			this.art_id = e.art_id;
			this.getDetailData();
		},
		methods:{
			getDetailData(){
				uniCloud.callFunction({
					name:"getArt_idData",
					data:{
						"art_id":this.art_id
					},
					success:(res)=>{
						this.artitleData = res.result.data[0];
					}
				})
			}
		}
	}
</script>

